const initState = {
  fetching: true,
  error: '',
  data: {
    recipesList: '',
    recipe: '',
    explore: '',
  },
};

function apiReducer(state = initState, { type, payload }) {
  switch (type) {
  case 'FETCHING':
    return {
      fetching: true,
      error: '',
      data: state.data,
    };

  case 'ERROR':
    return {
      fetching: false,
      error: payload,
      data: state.data,
    };

  case 'SUCCESS':
    return {
      fetching: false,
      error: '',
      data: { ...state.data, ...payload },
    };

  case 'CLEAR_API_DB':
    return initState;

  default:
    return state;
  }
}

export default apiReducer;
